<template>
  <div id="app">
    <div class="con_n">
      <my_nav></my_nav>
      <fixednav style="position:fixed;left:0;top:0;z-index:10" id="llink"></fixednav>
    </div>
    <div style="min-height:500px;margin:0 auto">
      <router-view></router-view>
    </div>
    <div class="app-footer">
      <p>咨询电话：00-10001000&nbsp; &nbsp; &nbsp;     咨询QQ：1000 1000 </p>
         <p>机构地址：上海市某某区某某人民路999号（中国）</p>
       <p>&nbsp; &nbsp; &nbsp;  &nbsp; &nbsp; &nbsp;&nbsp; &nbsp; &nbsp;                   多伦多市某某区某某路000号（加拿大）</p>     
    </div>
    <linki style="position:fixed;right:30px;bottom:0;width:300px;z-index:50"></linki>
  </div>
</template>

<script>
import linki from "./component/lianxi.vue"
import my_nav from "./component/my_nav.vue"
import fixednav from "./component/fixednav.vue"
export default {
  name: 'app',//有name引入的时候就叫什么，没有就自定义
  data () {
    return {
      type:true
    }
  },
  components:{
    my_nav,
    linki,
    fixednav,
  },
  created() {
    document.onscroll=function(){
      if(document.documentElement.scrollTop>=60){
        llink.style.display="block"
        console.log("oikedx")
      }else{
        llink.style.display="none"
      }
    }
    
  },
}
</script>

<style>
*{
  margin: 0;
  padding: 0;
  list-style: none;
  /* overflow-y: scroll */
}
.appn{
  height:420px;padding:20px;box-sizing:border-box;overflow-y:scroll;background:#fff;
  transition: 0.35s
}
input{
  outline: none
}
.clearfix::after,.clearfix::before{
  content: "";
  display: table;
}
.clearfix::after{
  clear: both;
}
.clearfix{
  zoom:1
}
.fl{
  float:left
}
.fr{
  float: right;
}
a{
  text-decoration: none
}
.con_n{
  width: 80%;
  margin: 0 auto;
}
.app-footer{
  /* height: 136px; */
  background-color: rgb(76, 76, 76);
  /* width: 50%; */
  margin: 0 auto;
  color: #a9a9a9;
  padding: 20px 0;
  line-height: 32px;
  font-size: 14px;
  text-align: center;
}
p{
  margin: 0 ;
}
</style>



