import Vue from 'vue'
import Router from 'vue-router'
import index from './routes/index.vue'
import lyzx from "./routes/lyzx.vue"
import yxzl from "./routes/yxzl.vue"
import zlzs from "./routes/zlzs.vue"
import lxkc from "./routes/lxkc.vue"
import gywm from "./routes/gywm.vue"
import lxwm from "./routes/lxwm.vue"

Vue.use(Router)
var routes = [
    {
        path:"/",
        name:"index",
        component:index
    },
    {
        path:"/lyzx",
        name:"lyzx",
        component:lyzx,

    },
    {
        path:"/yxzl",
        name:"yxzl",
        component:yxzl,        
    },
    {
        path:"/zlzs",
        name:"zlzs",
        component:zlzs,
              
    },
    {
        path:"/lxkc",
                name:"lxkc",
                component:lxkc
    },
    {
        path:"/gywm",
        name:"gywm",
        component:gywm,
    },{
        path:"/lxwm",
                name:"lxwm",
                component:lxwm
    }
]
export default new Router({
	routes
})


