        left: 20px;
    }
    .index-two{
        text-align: center;
        padding: 100px 0 80px;
    }
    .index-step,.in-advantage{
        padding: 100px 0 80px;
    }
    .in-advantage{
        background-color: #f7f7f7;
        padding-bottom: 150px;
    }
    .index-two h4,.index-team h4,.index-step h4,.in-advantage h4{
        text-align: center;
        font-size: 24px;
        line-height: 30px;
        /* padding-bottom: 10px; */
    }
    .index-st{
        font-family: arial;
    font-size: 14px;
    
    color: #c9c9c9;
    }
    .index-mk{
        width: 286px;
        height: 296px;
        box-sizing: border-box;
        padding: 50px 20px 0;
        border: 2px solid #ff655f;
        margin: 15px 4px;
    }
    .index-mk img{
        width: 50px;
        height: 45px;
        margin-bottom: 15px;
    }
</style>


