<template>
    <div class="clearfix" style="width:100%;background:#fff">
        <div  class="nav1_no1">
            <div v-for="(i,$index) in arr" style="float:left;margin: 0  25px;height:60px;position:relative" class="nav1-lin">
                 <a :href="i.path" :style="{'border-bottom':index==$index?'1px solid #000':'none','z-index':10}" @click="btn($index)" class="lin1" v-lj>
                    <span>{{i.title}}</span>
                    <span style="margin-left:10px" v-if="i.children" class="glyphicon glyphicon-menu-down"></span>
                </a>    
                <div :class="[$index==4?'nav1_xl1':'nav1_xl']" v-if="i.children" v-lj style="z-index:5;width:100px;padding-left:20px;padding-right:40px;box-sizing:content-box;">
                    <span v-for="(d,$dndex) in i.children" >
                        <a :href="d.path" @click="index=$index">{{d.title}}</a>
                    </span>
                </div>         
	        </div>
        </div>
    </div>
</template>

<script>
import bus from "../component/bus.vue"
import router from '../router.js'
export default {
    data() {
        return {
            index:0,
            arr: [
				{title:'首页',path:'#/'},
                {title:'留言咨询',path:'#/lyzx'},
                {title:'院线中心',path:'#/yxzl'},
                {
                    title:'专栏招生',
                    path:'#/zlzs',
                    children:[
                        {title:'留学课程',path:"#/lxkc"}
                    ]
                },
                {
                    title:'关于我们',
                    path:'#/gywm',
                    children:[
                            {title:'联系我们',path:"#/lxwm"}
                        ]
                }
                ],
                logo:"https://ccdn.goodq.top/caches/8f49422e0e95298cecff25e3af96c580/aHR0cHM6Ly81NjY3ZTUwMTIzY2UxLnQ3NC5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTYvMDYvZjk2YTFhY2ZlNDI1NjhjNjNjM2UxMjg3Mjk0NzZiZmQucG5n.png"
        }
    },
    directives:{
        lj(a,b){
           a.onmouseover=function(){
                $(a).parent().parent().children().children(".nav1_xl").css({"display":"none"})
               if(a.nextElementSibling){
                    a.nextElementSibling.style.display="block"
                }else{
                    a.style.display="block"
                }
           }
           a.onmouseout=function(){
               $(a).parent().parent().children().children('div').css({"display":"none"})
           }    
        }
    },
    methods: {
        btn(i){
            this.index=i
            bus.$on("lee",(a)=>{
            this.index=a
            console.log(this.index)
        })
        }
    },
    mounted() {
        bus.$on("lee",(a)=>{
            this.index=a
            console.log(this.index)
        })
    },
}
</script>

<style>
    .nav1_no1 a{
        color:#999999
    }
    .nav1_no1 a:hover{
        text-decoration: none;
        color:#999999
    }
    .nav1_no1{
    	float: right;  
        height:60px;
    }
     .lin1{
     	color: #999999;
         line-height: 60px;
         width: 100%;
         display:inline-block;
         text-decoration: none;
     }
     .nav1-lin:hover .lin1{
         border-bottom: 1px solid #000 !important
     } 
     #my_nav1 {
         line-height: 60px;
         height: 60px;
         width: 100%;
     }
     .nav1_logo{
         width: 262px;
         height: 40px;
         float: left;
         
     } 
     .nav1_logo img{
         width: 100%;
         height: 100%;
     }
     .nav1_xl1{
          position: absolute;
         left: -55px;
         top: 62px;
         display: none;
         background-color: rgba(221, 221, 221, 0.67);
         text-align: center;
         height: 30px;
         line-height: 30px;
         box-shadow: 1px 1px 1px 1px rgba(110, 100, 100, 0.5)
     }
     .nav1_xl{
         position: absolute;
         left: 5px;
         top: 62px;
         display: none;
         background-color: rgba(221, 221, 221, 0.67);
         text-align: center;
         height: 30px;
         line-height: 30px;
         box-shadow: 1px 1px 1px 1px rgba(110, 100, 100, 0.5)
     }
     .nav_xl a,.nav1_xl1 a{  
         display: inline-block;
         
     }
     .nav1_no1 .nav1_xl a{
         line-height: 26px;
     }
</style>


