<template>
    <div id="my_nav" class="clearfix">
        <div class="nav_logo">
            <img :src="logo" alt="">
        </div>
        <div  class="nav_no1">
            <div v-for="(i,$index) in arr" style="float:left;padding: 0  25px;height:60px;position:relative" class="nav-lin">
                 <a :href="i.path" :style="{color:index==$index?'#e24534':'#999999','z-index':5}" @click="btn($index)" class="lin" v-lj>
                    <span>{{i.title}}</span>
                    <span style="margin-left:10px" v-if="i.children" class="glyphicon glyphicon-menu-down"></span>
                </a>    
                <div class="nav_xl" v-if="i.children" v-lj style="z-index:5">
                    <span v-for="(d,$dndex) in i.children" >
                        <a :href="d.path" style="" @click="index=$index">{{d.title}}</a>
                    </span>
                </div>         
	        </div>
        </div>
    </div>
</template>

<script>
import bus from "../component/bus.vue"
import router from '../router.js'
export default {
    data() {
        return {
            index:0,
            arr: [
				{title:'首页',path:'#/'},
                {title:'留言咨询',path:'#/lyzx'},
                {title:'院线中心',path:'#/yxzl'},
                {
                    title:'专栏招生',
                    path:'#/zlzs',
                    children:[
                        {title:'留学课程',path:"#/lxkc"}
                    ]
                },
                {
                    title:'关于我们',
                    path:'#/gywm',
                    children:[
                            {title:'联系我们',path:"#/lxwm"}
                        ]
                }
                ],
                logo:"https://ccdn.goodq.top/caches/8f49422e0e95298cecff25e3af96c580/aHR0cHM6Ly81NjY3ZTUwMTIzY2UxLnQ3NC5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTYvMDYvZjk2YTFhY2ZlNDI1NjhjNjNjM2UxMjg3Mjk0NzZiZmQucG5n.png"
        }
    },
    directives:{
        lj(a,b){
           a.onmouseover=function(){
                $(a).parent().parent().children().children(".nav_xl").css({"display":"none"})
               if(a.nextElementSibling){
                //    console.log()                  
                    a.nextElementSibling.style.display="block"
                }else{
                    a.style.display="block"
                }
           }
           a.onmouseout=function(){
               $(a).parent().parent().children().children(".nav_xl").css({"display":"none"})
           }    
        }
    },
    methods: {
        btn(i){
            this.index=i
            bus.$on("lee",(a)=>{
            this.index=a
            console.log(this.index)
        })
        }
    },
    mounted() {
        bus.$on("lee",(a)=>{
            this.index=a
            console.log(this.index)
        })
    },
}
</script>

<style>
    .nav_no1{
    	float: right;  
         height: 60px;
    }
     .nav_no1 a{
     	color: #999999;
         line-height: 60px;
         width: 100%;
         display:inline-block;
         text-decoration: none;
     }
     .nav-lin:hover .lin{
         color: #e24534 !important;
     } 
     #my_nav {
         line-height: 60px;
         height: 60px;

         width: 100%;
     }
     .nav_logo{
         width: 262px;
         height: 40px;
         float: left;
         
     } 
     .nav_logo img{
         width: 100%;
         height: 100%;
     }
     .nav_xl{
         position: absolute;
         left: 27px;
         top: 60px;
         display: none;
         background-color: #fff;
         text-align: center;
         height: 26px;
         line-height: 26px;
         box-shadow: 1px 1px 1px 1px rgba(110, 100, 100, 0.5)
     }
     .nav_xl a{  
         padding-left:10px;
         padding-right:30px;
     }
     .nav_no1 .nav_xl a{
         line-height: 26px;
     }
</style>


